import React, {useEffect, useState} from 'react'
import Recipie from './components/recipe'
import './App.css'

export default function App() {

  const App_Id = '5b6b9285';
  const App_Key = 'f22a20a7858f6e421f5b39538a3f7e49';

  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState('')
  const [query, setQuery] = useState('egg')

  useEffect( () => {
    getData();
  }, [query] );

  const getData = async () => {
      const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${App_Id}&app_key=${App_Key}`)
      const data = await response.json();
      setRecipes(data.hits)
    }

    function inputHandler(e) {
      setSearch(e.target.value)
    }

    function getSearch (e) {
      e.preventDefault()
      setQuery(search)
      setSearch('')
    }

  return (
    <div className='App' >
     <form onSubmit={getSearch} className='search' >
     <input onChange={inputHandler} type='text' className='bar' value = {search} />
     <button type='submit' className='button' > Search </button>
     </form>
     <div className='recipes' >
     {recipes.map(recipe => (
      <Recipie 
        title = {recipe.recipe.label}
        img = {recipe.recipe.image}
        key={recipe.recipe.label}
        ing = {recipe.recipe.ingredients}
      />
     ))}
     </div>
    </div>
  )
}
