import React from 'react'

export default function recipe(props) {
    return (
        <div className='recipe'  >
            <h1>{props.title}</h1>
            <img className='img' src={props.img} alt={props.title} />
            <ul>
                {
                    props.ing.map( ing => (
                    <li>{ing.text}</li>
                    ))
            }
            </ul>
        </div>
    )
}
